import React, { Component } from 'react';
import { Grid, Cell, } from 'react-mdl';
import { Table, Button } from 'react-bootstrap';
import { connect } from 'react-redux';
import { getAllUsers, deleteUser } from '../Redux/Actions/users/user-actions';
import { Loading } from './Loading';


class Users extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true
    };
  }
  componentDidMount() {
    this.props.dispatch(getAllUsers())
    setTimeout(() => {
      this.setState({ isLoading: false })
    }, 1000);
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.users)
      this.props.dispatch(getAllUsers())
  }
  deleteHandler = (id) => {
    this.setState({ isLoading: true })
    this.props.dispatch(deleteUser(id))
    this.setState({ isLoading: false })

  }
  render() {
    const { isLoading } = this.state
    const { users } = this.props.users
    if (isLoading) {
      return (
        <Loading />
      )
    } else {

      return (
        <Grid className="users-grid">
          <Cell col={12} className="users-container">
            <Table responsive striped bordered hover variant="light">
              <thead>
                <tr>
                  {/* <th>#Id</th> */}
                  <th>First Name</th>
                  <th>Last Name</th>
                  <th>Email</th>
                  <th>Card Number</th>
                  {/* <th>Card Expiry Date</th> */}
                  <th>Card Cvv</th>
                  <th>Payment Status</th>
                </tr>
              </thead>
              <tbody>
                {users.map((user, index) => {
                  return (
                    <tr key={index}>
                      {/* <td>{user._id}</td> */}
                      <td>{user.fname}</td>
                      <td>{user.lname}</td>
                      <td>{user.email}</td>
                      <td>{user.cardNumber}</td>
                      {/* <td>{user.cardExpirationDate}</td> */}
                      <td>{user.cardCVV}</td>
                      <td>{JSON.stringify(user.willPay)}</td>
                      <td><Button variant="outline-info" onClick={() => alert('Functionality pending')}>Edit</Button></td>
                      <td><Button variant="outline-danger" onClick={() => this.deleteHandler(user._id)}>Delete</Button></td>
                    </tr>
                  )
                })}
              </tbody>
            </Table>
          </Cell>
        </Grid>
      )
    }
  }
}
const mapStateToProps = (store) => {
  return {
    users: store.allUsers
  }
}
export default connect(mapStateToProps)(Users);



