import { GET_ALL_FEEDBACKS, GET_ERRORS } from '../types';
import axios from 'axios';
export const getAllFeedbacks = () => dispatch => {
    axios
        .get("/api/admin/feedbacks")
        .then(res => {
            dispatch({
                type: GET_ALL_FEEDBACKS,
                payload: res.data
            })
        })
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        );
};


