import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import { Table, Button } from 'react-bootstrap';
import { getAllStaff, deleteStaff } from '../Redux/Actions/staff/staff-actions'
import { connect } from 'react-redux';
import { Loading } from './Loading';


class Staff extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      dataLoading: props.dataLoading
    }
  }
  componentDidMount() {
    this.props.dispatch(getAllStaff())
    setTimeout(() => {
      this.setState({ isLoading: false })
    }, 1000);
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    this.props.dispatch(getAllStaff())
  }
  deleteHandler = (id) => {
    this.setState({ isLoading: true })
    this.props.dispatch(deleteStaff(id))
    setTimeout(() => {
      this.setState({ isLoading: false })
    }, 1000);
  }
  render() {
    const { isLoading } = this.state
    const { staff } = this.props.staff
    if (isLoading) {
      return (
        <Loading />
      )
    } else {
      return (
        <Grid className="users-grid">
          <Cell col={12} className="users-container">
            <Table responsive striped bordered hover variant="light">
              <thead>
                <tr>
                  <th>#Id</th>
                  <th>First Name</th>
                  <th>Last Name</th>
                  <th>Email</th>
                </tr>
              </thead>
              <tbody>
                {staff.map((stf, index) => {
                  return (
                    <tr key={index}>
                      <td>{stf.staffID}</td>
                      <td>{stf.fname}</td>
                      <td>{stf.lname}</td>
                      <td>{stf.email}</td>
                      <td><Button variant="outline-info" onClick={() => alert('Functionality pending')}>Edit</Button></td>
                      <td><Button variant="outline-danger" onClick={() => this.deleteHandler(stf._id)}>Delete</Button></td>
                    </tr>
                  )
                })
                }
              </tbody>
            </Table>
          </Cell>
        </Grid>
      )
    }
  }
}
const mapStateToProps = (store) => {
  return {
    staff: store.allStaff,
    dataLoading: store.loading.isLoading
  }
}

export default connect(mapStateToProps)(Staff);



