import React, { Component } from 'react';
import { Form, Button } from 'react-bootstrap';
import { Grid, Cell } from 'react-mdl';
import icon from './icon.png'
import { loginUser } from '../../Redux/Actions/authActions';
import { connect } from 'react-redux';
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            errors: {}
        }
    }
    
    componentDidMount() {
        if (this.props.auth.isAuthenticated) {
            this.props.history.push('/dashboard')
        } 
    }
    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.auth.isAuthenticated) {
            this.props.history.push("/dashboard"); // push user to dashboard when they login
        }  
        if (nextProps.errors) {
            this.setState({
                errors: nextProps.errors
            });
        }
    }

    loginHandler = () => {
        const { email, password } = this.state
        const userData = {
            email,
            password
        }
        this.props.dispatch(loginUser(userData))
    }
    render() {
        const { email, password } = this.state
        return (
            <div>
                <Grid className="login-grid">
                    <Cell col={6}>
                        <div>
                            <img src={icon} height={'100px'} alt="brand Logo" />
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                            <h4 style={{ alignSelf: 'center' }}>Login</h4>
                        </div>
                        <Form style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center' }}>
                            <Form.Group controlId="formGroupEmail">
                                <Form.Control type="email" placeholder="Enter email" style={{ width: 300 }} value={email} onChange={e => this.setState({ email: e.target.value })} />
                            </Form.Group>
                            <Form.Group controlId="formGroupPassword">
                                <Form.Control type="password" placeholder="Password" style={{ width: 300 }} value={password} onChange={e => this.setState({ password: e.target.value })} />
                            </Form.Group>
                            <Form.Group controlId="formGroupPassword">
                                <Button style={{ width: '297px', backgroundColor: 'red', borderColor: 'red' }} onClick={this.loginHandler}>Login</Button>
                            </Form.Group>
                        </Form>
                    </Cell>
                </Grid>

            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
      auth: state.auth
    }
  }
  
export default connect(mapStateToProps)(Login)


