import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import { Table, Button } from 'react-bootstrap';
import { getAllComplaints } from '../../Redux/Actions/complaints/complaints-action'
import { connect } from 'react-redux';
import { Loading } from '../Loading';

class Complaints extends Component {
  state = {
    isLoading: true
  }
  componentDidMount() {
    this.props.dispatch(getAllComplaints())
    setTimeout(() => {
      this.setState({ isLoading: false })
    }, 1000);
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.allComplaints)
      this.props.dispatch(getAllComplaints())
  }
  render() {
    const { isLoading } = this.state
    const { allComplaints } = this.props.complaints
    if (isLoading) {
      return (
        <Loading />
      )
    }
    else {
      return (
        <Grid className="users-grid">
          <Cell col={12} className="users-container">
            <Table responsive striped bordered hover variant="light">
              <thead>
                <tr>
                  <th>Subject</th>
                  <th>Complaint</th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody>
                {allComplaints.map((comp, index) => {
                  return (
                    <tr key={index}>
                      <td>{comp.subject}</td>
                      <td>{comp.complaint}</td>
                      <td>{comp.status}</td>
                      <td><Button variant="outline-info" onClick={() => alert('functionality pending')}>Edit</Button></td>
                      <td><Button variant="outline-danger">Delete</Button></td>
                    </tr>
                  )
                })}
              </tbody>
            </Table>
          </Cell>
        </Grid>
      )
    }
  }
}

const mapStateToProps = (store) => {
  return {
    complaints: store.allComplaints
  }
}

export default connect(mapStateToProps)(Complaints);



