import { GET_ALL_COMPLAINTS } from "../../Actions/types";
const initialState = {};
export default function (state = initialState, action) {
    switch (action.type) {
        case GET_ALL_COMPLAINTS:
            return action.payload;
        default:
            return state;
    }
}


