import { GET_ALL_DEMANDS, GET_ERRORS } from '../types';
import axios from 'axios';
export const getAllDemands = () => dispatch => {
    axios
        .get("/api/admin/demands")
        .then(res => {
            dispatch({
                type: GET_ALL_DEMANDS,
                payload: res.data
            })
        })
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        );
};


