import { GET_ALL_FEEDBACKS } from "../../Actions/types";
const initialState = {};
export default function (state = initialState, action) {
    switch (action.type) {
        case GET_ALL_FEEDBACKS:
            return action.payload;
        default:
            return state;
    }
}


