import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import { connect } from 'react-redux';
import { Table, Button } from 'react-bootstrap';
import { getAllDemands } from '../../Redux/Actions/demands/demands-actions';
import { Loading } from '../Loading';

class OnDemands extends Component {
  state = {
    isLoading: true
  }
  componentDidMount() {
    this.props.dispatch(getAllDemands())
    setTimeout(() => {
      this.setState({ isLoading: false })
    }, 1000);
  }
  render() {
    const { isLoading } = this.state
    const { allDemands } = this.props.demands
    if (isLoading) {
      return (
        <Loading />
      )
    }
    else {
      return (
        <Grid className="users-grid">
          <Cell col={12} className="users-container">
            <Table responsive striped bordered hover variant="light">
              <thead>
                <tr>
                  <th>Title 1</th>
                  <th>Title 2</th>
                  <th>Title 3</th>
                </tr>
              </thead>
              <tbody>
                {allDemands.map((dem, index) => {
                  return (
                    <tr key={index}>
                      <td>{dem.title1}</td>
                      <td>{dem.title2}</td>
                      <td>{dem.title3}</td>
                      <td><Button variant="outline-info" onClick={() => alert('functionality pending')}>Edit</Button></td>
                      <td><Button variant="outline-danger">Delete</Button></td>
                    </tr>
                  )
                })}
              </tbody>
            </Table>
          </Cell>
        </Grid>
      )
    }
  }
}

const mapStateToProps = (store) => {
  return {
    demands: store.allDemands
  }
}

export default connect(mapStateToProps)(OnDemands);



