import { GET_ALL_COMPLAINTS, GET_ERRORS } from '../types';
import axios from 'axios';
export const getAllComplaints = () => dispatch => {
    axios
        .get("/api/admin/complaints")
        .then(res => {
            dispatch({
                type: GET_ALL_COMPLAINTS,
                payload: res.data
            })
        })
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        );
};



