import { GET_ALL_STAFF, GET_ERRORS, DELETE_STAFF, LOADING } from '../types';
import axios from 'axios';
export const getAllStaff = () => dispatch => {
    axios
        .get("/api/admin/staff/all_staff")
        .then(res => {
            dispatch({
                type: GET_ALL_STAFF,
                payload: res.data
            })
        })
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        );
};

export const deleteStaff = (id) => dispatch => {
    axios
        .delete(`/api/admin/staff/${id}`)
        .then(res => {
            dispatch({ 
                type: LOADING,
                payload: true            
            })
            dispatch({
                type: DELETE_STAFF,
                payload: res.data
            })
            
            dispatch({ 
                type: LOADING,
                payload: false          
            })
        })
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        );
};



