import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import { Table, Button } from 'react-bootstrap';
import { getAllFeedbacks } from '../../Redux/Actions/feedbacks/feedback-actions';
import { connect } from 'react-redux';
import { Loading } from '../Loading';


class Feedbacks extends Component {
  state = {
    isLoading: true
  }
  componentDidMount() {
    this.props.dispatch(getAllFeedbacks())
    setTimeout(() => {
      this.setState({ isLoading: false })
    }, 1000);
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.allFeedback)
      this.props.dispatch(getAllFeedbacks())
  }
  render() {
    const { isLoading } = this.state
    const { allFeedbacks } = this.props.feedbacks
    console.log(allFeedbacks)
    if (isLoading) {
      return (
        <Loading />
      )
    }
    else {
      return (
        <Grid className="users-grid">
          <Cell col={12} className="users-container">
            <Table responsive striped bordered hover variant="light">
              <thead>
                <tr>
                  <th>Title</th>
                  <th>Review</th>
                  <th>Ratings</th>
                </tr>
              </thead>
              <tbody>
                {allFeedbacks.map((feed, index) => {
                  return (
                    <tr key={index}>
                      <td>{feed.title}</td>
                      <td>{feed.review}</td>
                      <td>{feed.ratings}</td>
                      <td><Button variant="outline-info" onClick={() => alert('functionality pending')}>Edit</Button></td>
                      <td><Button variant="outline-danger">Delete</Button></td>
                    </tr>
                  )
                })}
              </tbody>
            </Table>
          </Cell>
        </Grid>
      )
    }
  }
}

const mapStateToProps = (store) => {
  return {
    feedbacks: store.allFeedback
  }
}

export default connect(mapStateToProps)(Feedbacks);



