import { GET_ALL_USERS, GET_ERRORS, DELETE_USER, LOADING } from '../types';
import axios from 'axios';
export const getAllUsers = () => dispatch => {
    axios
        .get("/api/admin/users/all_users")
        .then(res => {
            dispatch({
                type: LOADING,
                payload: true
            })
            dispatch({
                type: GET_ALL_USERS,
                payload: res.data
            })
            dispatch({
                type: LOADING,
                payload: false
            })
        })
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        );
};

export const deleteUser = (id) => dispatch => {
    axios
        .delete(`/api/admin/users/${id}`)
        .then(res => {
            console.log(res.data)
            dispatch({
                type: DELETE_USER,
                payload: res.data
            })
        })
        .catch(err =>
            dispatch({
                type: GET_ERRORS,
                payload: err.response.data
            })
        );
};



